<template lang="pug"> 
    .existed__group-schedule
        .existed__group-schedule__title Расписание
        ul.existed__group-schedule__list
            li(v-for='item,idx in days').existed__group-schedule__item {{item}}
                existed-group-schedule(
                    :day='idx+1'
                    :time='groupSchedule[idx].scheduleTime'
                    :editMode='editMode'
                    v-on:editSchedule='editSchedule'
                    :group='group'
                    )  
</template> 
<script>
import { mapMutations } from "vuex";
export default {  
    components: {
        existedGroupSchedule: () => import("../../admin/groups/existed-group-schedule")
    },
    data() {
        return {
            days: [
                'Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота', 'Воскресенье'
            ]
        }
    },
    props: {
        editMode: Boolean,
        group: Object, 
        groupSchedule: Array
    },
    methods: {
        ...mapMutations("groupSchedule", ["ADD_DATA", "REMOVE_DATA", "EDIT_DATA"]),
        editSchedule(data) {
            var elem = this.groupSchedule.find(ele=>ele.scheduleDay==data.scheduleDay)
            if(elem!=undefined){
                this.EDIT_DATA(data)
            } else {
                this.ADD_DATA(data)
            }
        }
    }
}
</script>
